<template>
    <div id="app" class="page-login">
        <div class="login-top">
            <div class="left">
                <img src="../assets/images/logo.png" alt="logo">
            </div>
        </div>
        <div class="content">
            <el-form :model="account" :rules="rules" ref="ruleForm" class="ruleForm">
                <h3 class="form-title">账号密码登录</h3>
                <el-form-item prop="WorkNumber">
                    <img src="../assets/images/login-user.png"/>
                    <el-input v-model="account.WorkNumber" type="text" placeholder="用户名"></el-input>
                </el-form-item>
                <el-form-item prop="Password" class="password">
                    <img src="../assets/images/login-key.png"/>
                    <el-input v-model="account.Password" type="password" placeholder="密码"></el-input>
                </el-form-item>
                <el-form-item class="remember">
                    <el-checkbox-group>
                        <el-checkbox  @change="rememberPassword" v-model="checked">记住密码</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item>
                    <el-button class="btn-login" type="primary" height="30" @click="isLogin()">立即登录
                    </el-button>
                </el-form-item>
                <a class="help-center" href="javascript:;">帮助中心</a>
            </el-form>
            <p class="support">技术支持：xxxx技术有限公司</p>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'LoginPage',
        data() {
            return {
                logining: false,
                account: {
                    WorkNumber: '',
                    Password: ''
                },
                checked: false,
                rules: {  //表单验证
                    WorkNumber: [
                        {required: true, message: '请输入账号', trigger: 'blur'}
                    ],
                    Password: [
                        {required: true, message: '请输入密码', trigger: 'blur'}
                    ]
                },
                isDownload: false
            }
        },
        computed: {
            user() {
                return this.$store.state.myUserInfo
            }
        },
        mounted() {
            this.getlocalStorage();
        },
        methods: {
            dataPost: async function () {
                var data = await this.$http.myPost(this.$code.URL_POST_LOGIN, this.account);
//                var data = await this.$http.myGet(this.$code.URL_GET_DEPARTMENT);
                this.$store.commit('isLogin', true);
                this.$store.commit('myUserInfo', data);
                if (window.localStorage) {
                    window.localStorage.setItem('myUserInfo', JSON.stringify(data))
                }
            },
            isLogin: async function () {
                if (this.account.WorkNumber != '' && this.account.Password != '') {
                    await this.dataPost();
                    this.$message.success({
                        message: '登录成功！'
                    });

                    if (this.checked) {
                        this.setlocalStorage(this.account);
                    } else {
                        localStorage.removeItem('myLoginInfo');
                    }
                    this.$router.push({ path: '/home' });

                    if( this.$store.state.myUserInfo){
                        var myUserInfo = this.$store.state.myUserInfo;
                        this.list = myUserInfo.permission;
                        this.userInfo = myUserInfo.user;
                    }

                } else {
                    this.$message.error({
                        message: '错误，账号或密码不能为空！'
                    });
                }
            },
            rememberPassword() {
                if (this.checked) {
                    this.setlocalStorage(this.account);
                } else {
                    localStorage.removeItem('myLoginInfo');
                }
            },
            setlocalStorage(account) {
                localStorage.getItem('myLoginInfo', account);
            },
            getlocalStorage: function () {
                if (localStorage.myLoginInfo) {
                    let loginInfo = JSON.parse(localStorage.getItem('myLoginInfo'));
                    this.account.WorkNumber = loginInfo.WorkNumber;
                    this.account.Password = loginInfo.Password;
                    this.checked = true;
                }

            },

        },

    }
</script>
<style lang="less" type='text/less'>
    @import '../assets/less/common';
    @loginColor: #87beff;
    .page-login {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #24344b;
        filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";
        -moz-background-size: 1334px 1080px;
        background-size: 1334px 1080px;
    }

    .login-top {
        height: 60px;
        line-height: 25px !important;
        background: #fff;
        overflow: hidden;
        padding: 10px 360px 0 !important;
        .left {
            float: left;
            img {
                width: 125px;
                height: 38px;
            }
        }
        .right {
            float: right;
            img {
                width: 30px;
                height: 34px;
                float: left;
                margin-top: 5px;
            }
            span {
                float: left;
            }
        }
        span {
            font-size: 16px;
            font-weight: bold;
            padding-top: 5px;
            margin-left: 5px;
            color: #24344b;
        }
    }

    .content {
        position: absolute;
        right: 360px;
        top: 210px;
        .ruleForm {
            width: 400px;
            height: 500px;
            border: 1px solid @loginColor;
            background: #fff;
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
            animation: main-move 0.6s;
            -ms-filter: "progid:DXImageTransform.Microsoft.Shadow(Strength=4, Direction=135, Color='#1b1b1b')";
        }
        .el-form-item {
            position: relative;
            text-align: center;
            img {
                position: absolute;
                left: 55px;
                top: 10px;
                z-index: 9;
            }
            .el-button {
                width: 320px;
                background: #87beff;
                border-color: #87beff;
            }
            input {
                padding: 0 15px 0 35px;
            }
        }
        .password {
            margin-bottom: 5px;
        }
        .remember {
            text-align: left;
            margin-left: 45px;
        }

        .el-form-item__error {
            left: 50px;
        }
        .el-input {
            width: 320px;

        }

        .form-title {
            margin: 80px 0 30px 0;
            text-align: center;
            font-size: 24px;
            color: @loginColor;
            font-weight: normal;
        }
        .login-tip {
            color: #0cdc0c;
            font-size: 14px;
            text-align: center;
            margin-bottom: 10px;
            position: absolute;
            top: 290px;
            left: 0;
            width: 100%;
        }
        .el-checkbox-group {
            height: 30px;
        }
        .el-checkbox {
            height: 30px;
        }
        .btn-login {
            margin: 20px auto 0 7px;
        }
        .help-center {
            font-size: 14px;
            color: #666;
            margin-top: 95px;
            text-align: center;
            display: block;
        }
        .support {
            font-size: 14px;
            color: #fff;
            margin-top: 80px;
            text-align: center;
        }
        .login-tip-show {
            .login-tip {
                display: block;
            }
            .btn-login {
                margin-top: 10px;
            }
        }
    }
</style>